/* FONTS */
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;500;700&family=Roboto:wght@400;500;700&display=swap');

/* RESET */
* {
  margin: 0;padding: 0;
  font-family: 'Noto Sans KR', 'Roboto', sans-serif; color: #2d2d2d; line-height: 1.6;
  box-sizing: border-box; outline: none;
  fill: inherit;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, tbody, tfoot, thead, tr, th, td{margin:0;padding:0;}
h1, h2, h3, h4, h5, h6 {font-size: inherit;}
a {color: inherit; text-decoration: inherit;}
img {vertical-align: middle;}
a img {border: none;}
li {list-style: none;}
address, em, i {font-style: normal;}

/* COMMON */
.App{
  height: 100vh;
}

/* SCROLL BAR */
/* width */
::-webkit-scrollbar {width: 10px;}
/* Track */
/* ::-webkit-scrollbar-track {} */
/* Handle */
::-webkit-scrollbar-thumb {background: rgba(116, 185, 255, .5); border-radius: 10px;}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {background: rgba(237, 66, 100, .6);}

