/* NAV */
.nav{min-width: 260px; height: 100%; padding-top: 1px; background-color: rgba(2, 52, 63, .4); position: relative;}
.nav ul{margin-top: 12px; width: 100%;}
.nav ul li{display: block; height: 40px; padding-left: 12px; font-size: 18px; line-height: 40px; transition: .2s ease-in-out; cursor: pointer;}
.nav ul li:hover{background-color: #2d2d2d; color: #fff; text-decoration: none;}

.nav .signedUser{display: -webkit-flex;display: flex; -webkit-align-items: center; align-items: center; margin-left: 12px; position: absolute; bottom: 80px;}
.nav .signedUser .signedUser--img{width: 70px; height: 70px; margin-right: 12px; border-radius: 50%;}
.nav .signedUser .signedUser--txt{cursor: pointer;}
.nav .signedUser .signedUser--txt .signedUser__email{font-size: 12px;}

.nav footer{margin-left: 12px; position: absolute; bottom: 12px; font-size: 12px;}
.nav .footer--top{letter-spacing: 1.4px;}
.nav .footer--bot{letter-spacing: 1.1px;}

/* MAIN */
.ClientMain {width: 100%; margin: 100px 80px; position: relative;}

/* RankingContent */
.rankingContainer{width: 100%; max-height: 100%;   overflow: auto; scrollbar-color: rgba(116, 185, 255, .5) rgba(255, 255, 255, 0); scrollbar-width: thin;}
.ranking{display: -webkit-flex;display: flex; -webkit-align-items: center; align-items: center; height: 100px; margin: 28px 20px 28px 10px; position: relative; cursor: pointer;}
.ranking .grade{width: 120px; font-size: 84px; font-weight: 500; text-align: center;}

.ranking .item--img{width: 80px; height: 80px; position: absolute; left: 150px;}
.ranking .item--img img{width: 100%; height: 100%; border-radius: 50%; box-shadow: rgba(0, 0, 0, 0.35) 0px 2px 6px;}

.ranking .item--right{display: -webkit-flex;display: flex; position: absolute; right: 0;}
.ranking .item--right .rating{margin-right: 48px; font-size: 68px;}
.ranking .item--right > div{min-width: 140px; position: relative;}
.ranking .item--right > div .studentName{width: 100%; font-size: 36px; text-align: center; position: absolute; top: 8px;}
.ranking .item--right > div .studentId{position: absolute; bottom: 16px; left: 8px;}
.ranking .item--right > div .department{position: absolute; bottom: 16px; right: 8px;}

/* rankingContent DROPBOX */
.dropdown{width: 100px;  position: absolute; top: -60px; right: 160px; cursor: pointer;}
.dropdown:hover .dropdown--list{opacity: 1;visibility: visible;}
.dropdown .dropdown--selected{display: -webkit-flex;display: flex; -webkit-justify-content: space-between; justify-content: space-between; -webkit-align-items: center; align-items: center; width: 100%; height: 40px; padding: 0 16px; background-color: rgba(255, 255, 255, .7); border-radius: 5px;}
.dropdown .dropdown--list{background-color: rgba(255, 255, 255, .9); border-radius: 5px; position: absolute; top: 45px; left: 0; right: 0; z-index: 99; opacity: 0; visibility: hidden; transition: all .1s linear;}
.dropdown .dropdown--list .dropdown--list__item{margin: 8px 0; padding: 8px 16px; font-weight: 500;}
.dropdown .dropdown--list .dropdown--list__item:hover{background-color: rgba(180, 180, 180, 0.8);}

/* INPUT in rankingContent */
.filter--rangkingContent{width: 130px; height: 40px; padding: 8px 16px; background-color: rgba(255, 255, 255, .7); border: none; border-radius: 5px; position: absolute; top: -60px; right: 20px;}

/* UserInfo */
.usersInformation {width: 90%; height: 100%; margin: 0 auto; overflow: auto; scrollbar-color: rgba(237, 66, 100, .6) rgba(255, 255, 255, 0); scrollbar-width: thin}
/* .userList{float: left;} */
.userList {display: -webkit-flex;display: flex; -webkit-flex-wrap: wrap; flex-wrap: wrap;}

.userInfo {display: -webkit-flex;display: flex; -webkit-align-items: center; align-items: center; width: 300px; height: 120px; margin: 16px; padding: 4px; border-radius: 4px; background-color: rgba(255,255,255,0.5);}
.userInfo .userImage{width:84px; height: 108px; border-radius: 40%; cursor: pointer;}

.userInfo .userInfoRightSide{display: -webkit-flex;display: flex; -webkit-flex-direction: column; flex-direction: column; -webkit-justify-content: space-between; justify-content: space-between; width:100%; height: 100%; padding: 16px;}
.userInfo .userInfoRightSide .userInfoUpSide{display: -webkit-flex;display: flex; -webkit-justify-content: space-between; justify-content: space-between;}
.userInfo .userInfoRightSide .userInfoUpSide .userStatus{margin-left: 4px; padding: 0 2px; background-color: #02343F; border-radius: 2px; font-size: 11px; color: #F0EDCC;}

.userInfo .userInfoRightSide .userInfoUpSide .win_lose {display: -webkit-flex;display: flex; border-bottom: 1px solid #A4193D;}
.userInfo .userInfoRightSide .userInfoUpSide .win_lose .department:last-child{margin-left: 4px;}

.userInfo .userInfoRightSide .userInfoBottomSide{display: -webkit-flex;display: flex; -webkit-justify-content: space-between; justify-content: space-between; -webkit-align-items: baseline; align-items: baseline;}
.userInfo .userInfoRightSide .userInfoBottomSide .rating{font-size: 16px;}
.userInfo .userInfoRightSide .userInfoBottomSide .department{font-size: 12px;}

.filter--info{width: 130px; height: 40px; padding: 8px 16px; background-color: rgba(255, 255, 255, .7); border: none; border-radius: 5px; position: absolute; top: -60px; right: 80px;}

/* UserDetail */
.detailContainer{position: absolute; top: 50px; left: 50px; bottom: 50px; right: 50px; padding: 24px 36px; border-radius: 6px; background-color: #DCE2F0; transition: .4s ease-in-out;}
.detailContainer .close{position: absolute; top: 12px; right: 12px; font-size: 32px; transition: .4s; cursor: pointer;}
.detailContainer .close:hover{-webkit-transform: rotate(-180deg);transform: rotate(-180deg); fill: #e74c3c;}

.detailContainer .top{}
.detailContainer .top .top--left{display: -webkit-flex;display: flex; width: 50%;}
.detailContainer .top .top--left div:nth-child(2){width: calc(100% - 180px); position: relative;}
.detailContainer .top .top--left .detail--profile{width: 180px; height: 180px; margin-right: 24px;}
.detailContainer .top .top--left .detail--profile img{width: 100%; height: 100%;}

.detailContainer .top .top--left .detail__name{position: absolute; top: 0%; font-size: 48px; letter-spacing: 5px;}
.detailContainer .top .top--left .detail__department{position: absolute; top: 60%; -webkit-transform: translateY(-50%); transform: translateY(-50%); font-size: 24px; letter-spacing: 2px;}
.detailContainer .top .top--left .detail__status{position: absolute; bottom: 0; font-size: 16px;}














