/* RECENT GAMES */
.aside1{height: 50%; margin: 16px 8px 16px 0; overflow: auto; scrollbar-color: rgba(237, 66, 100, .6) rgba(255, 255, 255, 0); scrollbar-width: thin;}

.games{margin-top: 8px; margin-right: 16px;}
.games .game{margin-bottom: 16px; padding: 8px; background-color: rgba(255, 255, 255, .9); border-radius: 4px;}
.games .game .top{margin-bottom: 4px; font-size: 12px;}

.games .game .bot .winner > span{display: block; width: 100%; font-size: 12px;}
.games .game .bot .winner .winnerInfo{display: -webkit-flex;display: flex; -webkit-justify-content: space-between; justify-content: space-between; margin-bottom: 4px;}
.games .game .bot .winner .winnerInfo .winnerNames{display: -webkit-flex;display: flex; -webkit-flex-direction: column; flex-direction: column; min-width: 100px; margin-left: 8px;}
.games .game .bot .winner .winnerInfo .winnerRatings{display: -webkit-flex;display: flex; -webkit-flex-direction: column; flex-direction: column;}
.games .game .bot .winner .winnerInfo .winnerRatings .icon--win{margin-left: 8px; fill: #2ed573;}

.games .game .bot .loser > span{display: block; width: 100%; font-size: 12px;}
.games .game .bot .loser .loserInfo{display: -webkit-flex;display: flex; -webkit-justify-content: space-between; justify-content: space-between;}
.games .game .bot .loser .loserInfo .loserNames{display: -webkit-flex;display: flex; -webkit-flex-direction: column; flex-direction: column; min-width: 100px; margin-left: 8px;}
.games .game .bot .loser .loserInfo .loserRatings{display: -webkit-flex;display: flex; -webkit-flex-direction: column; flex-direction: column;}
.games .game .bot .loser .loserInfo .loserRatings .icon--lose{margin-left: 8px; fill: #ff4757;}

