/* Post */
.postMain{height: 100%;}
.postList{height: 100%; overflow: scroll; width: 60%; background-color: rgba(240,240,240,1); transition: 0.5s;}
.postList.active {opacity: 0.1;pointer-events: none;-webkit-user-select: none;-ms-user-select: none;user-select: none; transition: 0.5s;}
.post{margin: 16px;padding: 10px; background-color: white; box-shadow : 2px 2px 5px #999;}
.writeHidden { visibility: hidden; max-height: 0px;}
.writeMode {height: 5%;}
.titleInput { margin:8px; width: 70%; height: 40px;}
.buttons{width: 100%; text-align: center; border-top: 1px black solid;}
.submitButton { width: 64px; height: 24px; font-size: 16px; border: none; background: white; border-radius: 5px; margin-top: 12px;}
.writeModeBtn {height: 70%; width: auto; margin: 8px; padding-right: 12px; padding-left: 12px; border-radius: 10%;}
.postMakeBtn{ margin: 16px; padding:10px;background-color: white; box-shadow : 2px 2px 5px #999; display: -webkit-flex; display: flex; -webkit-align-items: center; align-items: center; -webkit-justify-content: space-between; justify-content: space-between;}
.userProfile{width: 48px; height: 48px; border-radius: 50%;}
.postHeader{display: -webkit-flex;display: flex;}
.postHeaderLeft{margin: 8px;}
.postHeaderRight{margin: 8px;}
.postDate{font-size: large;}
.postContent{margin: 12px;}
.makePost{padding: 12px; border: none; box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);width: 100%; height: 100%;}
.postMaker {background-color: white;position: fixed; top: 0; left: 20%;visibility: hidden; opacity: 0; transition: 0.5s; width: 40%; height: 50%; margin: 0; display: -webkit-flex; display: flex; -webkit-flex-direction: column; flex-direction: column; -webkit-align-items: center; align-items: center; border: 1px solid black; border-radius: 16px;}
.postMaker.active { height: 60%; visibility: visible; opacity: 1; transition: 0.5s; top: 10%; z-index: 1;}
.postMakeHeader{border-bottom: 1px solid gray; padding: 12px; width: 100%; text-align: center; font-size:large; font-weight: 400;}
.file{width: 100%; height: 40%; padding: 8px;}
.fileHeader{margin-bottom:8px;}
.fileInput{display: none;}
.images{width: 20%; height: auto}
.postImage{width: 100px;}
